body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.home{
    position: relative;
    height: 100vh;
    align-items: center;
    display:flex;
    justify-content: center;
}

.container {
    position: absolute;
    top: 30%;
    width: 100%;
    

}

.welcome{
    font-size: 2rem;
    color:   rgb(142, 142, 142);
    font-display: bold;
    text-shadow: -1px 1px 2px white,
    1px 1px 2px white,
    1px -1px 0 white,
    -1px -1px 0 white;

}



.co-links{
    color: rgb(52, 50, 50);
    text-decoration: none;

}
.co-list-section{
position: absolute;
max-height: 100vh;

}


.job-list-section{
    width: 100vw;
    position: absolute;
    max-height: 100vh;
 
    }
    
    
