.App {
  text-align: left;
  background-image: url(/static/media/blue.563b37fc.jpg);
  background-position: center;
  background-size: 100%;
  background-repeat: no-repeat;
  width: 100%;
  height: 100vh;
  position: fixed;
  overflow: scroll;
  }


nav {
  background-color: white;
  box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12), 0 1px 5px 0 rgba(0,0,0,0.2);
}

.nav-item.logout {
  width: 200px;
  text-align: center;
}

.nav-item {
  width: 100px;
  text-align: center;
}


nav a {
  color: rgb(162, 162, 162);
  text-decoration: none;
}

nav a.navbar-brand{
    color: black;
    border-bottom: none;
    font-weight: bold;
}

nav a.active:not(.navbar-brand) {
  color:  rgb(75, 75, 75);
  font-weight: bold;
  box-sizing: border-box;
}

nav a.active:not(.navbar-brand):hover {
  color: rgb(75, 75, 75) !important;
}

nav a:not(.navbar-brand):hover {
    text-decoration: none;
   color: rgb(131, 131, 131);
  font-weight: bold;
}


